import {
	getRichText
} from '@/api'

import {
	RICH_TEXT
} from './mutation-types'

export default {
	//  252627
	async reqGetRichText ({commit}, params) {
		uni.showLoading({
			title: '....'
		})
		const result = await getRichText (params);
		uni.hideLoading()
		commit(RICH_TEXT, result.data.content)
	}
}